import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import About from '@/views/About'
import My from '@/views/My'

Vue.use(Router)

const router = new Router({
  routes: [{
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '',
      redirect: '/home'
    }
  ],
  // mode: 'history'
})
export default router
router.beforeEach((to, from, next) => {
  next();
})



